
@import url('https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300');
#navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  min-height: 340px;
}
#navigation .navColumn {
  color: #826849;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  max-width: none;
  min-width: -webkit-min-content;
  min-width: -moz-min-content;
  min-width: min-content;
  margin: 2vh 1.5vw;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;

  -webkit-transition: .5s ease;

  transition: .5s ease;
}
#navigation .navColumn h3 {
  padding: 0 20px;
  font-size: 20px;
  width: 100%;
  background-color: rgba(250, 235, 215, 0.49);
}
#navigation .navColumn .navPicture {
  max-height: 50vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  width: 100%;
  min-height: 70vh;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;

  background-position: center;
  background-size: cover;

  border: white 1px solid;

  cursor: pointer;

  -webkit-transition: all .5s ease;

  transition: all .5s ease;
}
#navigation .navColumn .navPicture.small {
  min-height: 10vh;
}
#navigation .navColumn .navPicture:hover {
  -webkit-transform: scale(1.04);
      -ms-transform: scale(1.04);
          transform: scale(1.04);
}
#navigation .navColumn:nth-child(1) .navPicture {
  background-image: URL('../../static/media/albin.jpg');
}
#navigation .navColumn:nth-child(2) .navPicture {
  background-image: URL('../../static/media/shirin.jpg');
  color: #403427;
}
#navigation .navColumn:nth-child(3) .navPicture {
  background-image: URL('../../static/media/shirin2.jpg');
}
#navigation .navColumn:nth-child(4) .navPicture {
  background-image: URL('../../static/media/sto.jpg');
}
#navigation .navColumn:nth-child(5) .navPicture {
  background-image: URL('../../static/media/kli.jpg');
  background-position: 51%;
}


/* MOBILE DEVICES */
@media only screen and (max-device-width: 480px) {
  /* define mobile specific styles come here */
#navigation .navColumn {
    min-width: 98vw;
}
#navigation .navColumn h3 {
    font-size: 40px;
}
#navigation .navColumn .navPicture {
    min-height: 20vh;
}
}


/* TABLETS */
@media only screen and (max-device-width: 768px) and (min-device-width: 480px) {
#navigation .navColumn {
    min-width: 40vw;
}
#navigation .navColumn h3 {
    font-size: 40px;
}
#navigation .navColumn .navPicture {
    min-height: 40vh;
}
}



